* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
  background-color: #dad7d7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
  width: 100vw;
}

.app-main-container {
  width: 90%;
  height: 85%;
  margin-top: 1rem;
  border-radius: 0.5rem;
  background-color: rgba(150, 150, 150, 0.4);
}

@media only screen and (max-width: 768px) {
  .app-main-container {
    width: 95%;
  }
}

@media only screen and (max-width: 576px) {
  .app-main-container {
    width: 98%;
    height: 100%;
    margin-top: 0.5rem;
    border-radius: 0.5rem 0.5rem 0 0;
  }
}

.Nav {
  width: 90%;
  height: auto;
  margin-top: 0.3rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 0.3rem;
  padding: 0;
}

.Nav .logo .logo-tab {
  color: rgba(0, 0, 30, 1);
  font-size: 4rem;
  font-weight: bold;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Nav .logo,
.Nav .navs {
  width: 50%;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Nav .tab {
  font-size: 3.5rem;
  height: inherit;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  color: rgba(0, 0, 50, 0.8);
  padding: 0.2rem 1rem;
  margin: 0 0.2rem;
  border-radius: 0.5rem;
  white-space: nowrap;
}

.Nav .tab:hover {
  background-color: rgba(0, 0, 30, 0.3);
}

.Nav .tab.active {
  color: rgba(0, 0, 30, 1);
  background-color: rgba(0, 0, 30, 0.4);
}

@media only screen and (max-width: 768px) {
  .Nav {
    width: 95%;
  }

  .Nav .logo .logo-tab {
    font-size: 2.5rem;
  }

  .Nav .tab {
    font-size: 2.5rem;
  }
}

@media only screen and (max-width: 576px) {
  .Nav {
    width: 100%;
    justify-content: space-between;
  }

  .Nav .logo {
    width: 30%;
  }

  .logo-tab {
    font-size: 2rem;
  }

  .Nav .navs {
    width: 70%;
    justify-content: flex-end;
    padding-right: 1rem;
  }

  .Nav .tab {
    font-size: 2rem;
  }
}

.home {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  -webkit-animation: animateHome 2s;
          animation: animateHome 2s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  color: rgba(0, 0, 30, 1);
}

@-webkit-keyframes animateHome {
  0% {
    opacity: 0;
  }
  100% {
    apacity: 1;
  }
}

@keyframes animateHome {
  0% {
    opacity: 0;
  }
  100% {
    apacity: 1;
  }
}

.home .home-img {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-image: url(/static/media/background.447c4864.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 0 0.5rem 0.5rem 0;
}

.home .home-info {
  width: 50%;
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.home .home-info p {
  text-transform: uppercase;
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  font-weight: 700;
  border: 0.1rem solid rgba(0, 0, 30, 0.6);
  padding: 0.5rem;
  border-radius: 1rem;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-animation-name: pBorder;
          animation-name: pBorder;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes pBorder {
  0% {
    border-color: rgba(0, 0, 30, 0.8);
  }

  25% {
    border-color: rgba(0, 0, 30, 0.5);
  }

  50% {
    border-color: rgba(0, 0, 30, 0.8);
  }

  75% {
    border-color: rgba(0, 0, 30, 0.1);
  }

  100% {
    border-color: rgba(0, 0, 30, 1);
  }
}

@keyframes pBorder {
  0% {
    border-color: rgba(0, 0, 30, 0.8);
  }

  25% {
    border-color: rgba(0, 0, 30, 0.5);
  }

  50% {
    border-color: rgba(0, 0, 30, 0.8);
  }

  75% {
    border-color: rgba(0, 0, 30, 0.1);
  }

  100% {
    border-color: rgba(0, 0, 30, 1);
  }
}

.home .home-info h1 {
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  font-size: 4rem;
  font-weight: 700;
}

.home .home-info h3 {
  font-size: 3rem;
  font-weight: bold;
}

.home .home-info .icons {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.home .home-info .icons a {
  font-size: 4rem;
  color: inherit;
  opacity: 1;
}

.home .home-info .icons a:hover {
  opacity: 0.6;
  transition: opacity 0.5s ease-out;
}

@media only screen and (max-width: 864px) {
  .home .home-info h1 {
    font-size: 3rem;
  }

  .home .home-info h3 {
    font-size: 2.5rem;
  }

  .home .home-info .icons a {
    font-size: 3rem;
  }
}

@media only screen and (max-width: 768px) {
  .home .home-info p {
    font-size: 1.2rem;
  }

  .home .home-info h1 {
    font-size: 2.5rem;
  }

  .home .home-info h3 {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 576px) {
  .home {
    flex-direction: column;
  }

  .home .home-img {
    width: 100%;
    height: 60%;
    border-radius: 0.5rem 0.5rem 0 0;
  }

  .home .home-info {
    width: 100%;
    height: 40%;
  }

  .home .home-info p {
    font-size: 1rem;
  }

  .home .home-info h1 {
    font-size: 2rem;
  }

  .home .home-info h3 {
    font-size: 1.5rem;
  }

  .home .home-info .icons {
    width: 70%;
  }

  .home .home-info .icons a {
    font-size: 2.5rem;
  }
}

.about-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-animation: animateAbout 2s;
          animation: animateAbout 2s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  color: rgba(0, 0, 30, 1);
}

@-webkit-keyframes animateAbout {
  0% {
    opacity: 0;
  }
  100% {
    apacity: 1;
  }
}

@keyframes animateAbout {
  0% {
    opacity: 0;
  }
  100% {
    apacity: 1;
  }
}

.about {
  width: 50%;
  height: 100%;
  padding: 1rem;
  border-right: 0.1rem solid white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about h1,
.skills h1 {
  text-transform: uppercase;
  font-size: 3rem;
}

.about hr,
.skills hr {
  width: 20%;
  height: 0.3rem;
  background-color: rgb(0, 0, 20);
  border: 0.1rem solid rgb(0, 0, 20);
  border-radius: 20%;
}

.about p {
  font-size: 1.8rem;
  font-weight: 600;
  margin-top: 2rem;
  color: rgb(0, 0, 20);
}

.skills {
  width: 50%;
  height: 100%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.skills-container {
  font-size: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 70%;
  width: 90%;
}

.skills-container hr {
  width: 30%;
  height: 0.5rem;
  background-color: rgb(0, 0, 20);
  border: 0.1rem solid rgb(0, 0, 20);
  border-radius: 100%;
}

.basic-lang,
.frontend,
.deep-learning {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
}

@media only screen and (max-width: 768px) {
  .about h1,
  .skills h1 {
    font-size: 2.5rem;
  }

  .about hr,
  .skills hr {
    width: 20%;
  }

  .about p {
    font-size: 1.5rem;
  }

  .skills-container {
    font-size: 3.5rem;
  }
}

@media only screen and (max-width: 576px) {
  .about-container {
    flex-direction: column;
  }

  .about {
    width: 100%;
    height: 35%;
    border-right: 0;
    border-bottom: 0.1rem solid white;
  }

  .about h1,
  .skills h1 {
    font-size: 2rem;
    letter-spacing: 0.2rem;
  }

  .about hr,
  .skills hr {
    width: 13%;
    height: 0.4rem;
  }

  .about p {
    font-size: 1.3rem;
    letter-spacing: 0.1rem;
    margin-top: 1rem;
  }

  .skills {
    width: 100%;
    height: 65%;
  }

  .skills-container {
    font-size: 3rem;
    height: 100%;
    width: 80%;
  }

  .skills-container hr {
    width: 20%;
  }
}

.contact {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  -webkit-animation: animateContact 2s;
          animation: animateContact 2s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  color: rgba(0, 0, 30, 1);
  font-size: 4rem;
}

@-webkit-keyframes animateContact {
  0% {
    opacity: 0;
  }
  100% {
    apacity: 1;
  }
}

@keyframes animateContact {
  0% {
    opacity: 0;
  }
  100% {
    apacity: 1;
  }
}

.github a,
.social-media a {
  color: inherit;
  opacity: 1;
}

.github a:hover,
.social-media a:hover {
  opacity: 0.6;
  transition: opacity 0.5s ease-out;
}

.github {
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-media {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 30%;
}

.social-media a {
  display: inline-flex;
}

.email,
.mobile {
  font-size: 3rem;
}

.email {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.email hr,
.mobile hr {
  width: 8%;
  height: 0.2rem;
  background-color: rgb(0, 0, 20);
  border-color: transparent;
}

.email p,
.mobile p {
  font-size: 2rem;
  font-weight: 600;
}

.mobile {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mobile hr {
  margin-top: 0.5rem;
}

@media only screen and (max-width: 576px) {
  .contact {
    font-size: 3.5rem;
  }

  .social-media {
    width: 60%;
  }

  .email,
  .mobile {
    font-size: 2.5rem;
  }

  .email p,
  .mobile p {
    font-size: 1.5rem;
  }
}

